.item {
   display: flex;
   align-items: center;
   width: 250px;
   padding: 10px;
   border-radius: 5px;
   background-color: #fff;
   border: 1px solid rgb(197, 195, 195);
   box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.item:not(:last-child) {
   margin-bottom: 8px;
}

.image {
   margin-right: 10px;
}

.status {
   width: 10px;
   height: 10px;
   border-radius: 50%;
   margin-right: 10px;
}

.text {
   font-style: italic;
   font-weight: 500;
   color: hsla(0, 0%, 0%, .7);
}