.section {
   width: 250px;
   padding-top: 20px;
   margin: 0 auto 30px;
   border-radius: 5px;
   background-color: rgb(212, 212, 212);
   box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;

}

.title {
   font-style: italic;
   text-align: center;
   margin-bottom: 15px;
   color: hsla(0, 0%, 0%, .7);
}

.list {
   display: flex;
}

.item {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   width: 50px;
   height: 50px;
}

.label {
   font-size: 12px;
   margin-bottom: 8px;
   color: rgb(217, 226, 226);
}

.percentage {
   font-size: 14px;
   color: rgb(217, 226, 226);
}