.list {
   display: flex;
   flex-direction: column;
   width: 270px;
   padding: 10px;
   margin: 0 auto 30px;
   border-radius: 5px;
   background-color: rgb(236, 233, 233);
   box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
   border: 1px solid rgb(197, 195, 195);
}